.header {
  padding: 15px 0 25px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  max-width: 1280px;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  row-gap: 20px;
}

@media screen and (max-width: 625px) {
  .header {
    padding-top: 28px 0 0;
  }
}
