.slick-prev {
  z-index: 1;
  left: 25%;
  top: 10px;
}
.slick-next {
  z-index: 1;
  right: 25%;
  top: 10px;
}

.slick-slide {
  height: 75%;
}

.slick-dots {
  bottom: -30px;
}

.slick-dots li button:before {
  color: white;
  background-color: transparent;
}

.slick-dots li.slick-active button:before {
  color: red;
  background-color: transparent;
}
@media screen and (max-width: 1100px) {
  .slick-slider {
    max-height: 80vh;
  }
}
@media screen and (max-width: 780px) {
  .slick-prev {
    left: 20%;
  }
  .slick-next {
    right: 20%;
  }
  /* .slick-slider {
    max-height: 80vh;
  } */
}

@media screen and (max-width: 550px) {
 
  .slick-prev {
    left: 15%;
  }
  .slick-next {
    right: 15%;
  }
}
